import { Component, ViewChild } from '@angular/core';
import {ViewController} from 'ionic-angular';
import * as firebase from 'firebase';
import { TabsPage } from '../tabs/tabs';
import { map } from 'rxjs/operators/map';
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs/Observable";

/**
 * Generated class for the CompleteProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@Component({
  selector: 'page-complete-profile',
  templateUrl: 'complete-profile.html',
})
export class CompleteProfilePage {

  loading: any;
  language = [];
  user: any;
  lang: any;
  username: any;

  constructor(
    private http: HttpClient,
    public viewCtrl: ViewController
  ) {

    this.GetLanguage()
      .subscribe(res => {
        this.language = res;
      });
    this.user = JSON.parse(localStorage.getItem('user'));
    if (this.user) {
      firebase.firestore().collection('users').doc(this.user.$id).get().then(user => {
        this.user = user.data();
        this.username = this.user.username;
        this.lang = this.user.language;
      })
    }
  }

  GetLanguage() {
    const msgListUrl = './assets/language/language.json';
    return this.http.get<any>(msgListUrl)
      .pipe(map(response => response.array));
  }

  complete(lang, username) {
    var self = this;
    this.user.language = lang;
    this.user.username = username.toLowerCase();
    this.user.complete = true;
    let imageData = '';
    firebase.storage().ref('users').child(this.user.$id).child('thumb.jpg')
      .putString(imageData, 'base64').then((snapshot) => {
      firebase.storage().ref('users').child(self.user.$id).child('thumb.jpg').getDownloadURL().then((downloadURL) => {
        this.user.thumb = downloadURL;
        firebase.firestore().collection('users').doc(this.user.$id).update(this.user);
        localStorage.setItem('user', JSON.stringify(this.user));
        this.viewCtrl.dismiss(this.user);
      })
    })
  }

}



